<template>
  <div class="mailDetail">
    <div class="flex-3 mail-left mail-card">
      <div class="flex-1 mail-item">
        <div
          class="item-title"
          style="background:rgb(138, 17, 17)"
        >钓鱼邮件</div>
        <div class="item-content back-icon">
          <RollbackOutlined @click="goBack" />
        </div>
      </div>
      <div class="flex-1 mail-item">
        <div class="item-title">邮件标题</div>
        <div class="item-content">{{ mailInfo.title }}</div>
      </div>
      <div class="flex-1 mail-item">
        <div class="item-title">发件人</div>
        <div class="item-content"><a style="color:rgba(22,130,252,1)">{{ mailInfo.senders }}</a></div>
      </div>
      <div class="flex-1 mail-item">
        <div class="item-title">发件时间</div>
        <div class="item-content">{{ mailInfo.sendDate }}</div>
      </div>
      <div class="flex-1 mail-item">
        <div class="item-title">收件人</div>
        <div class="item-content"><a style="color:rgba(22,130,252,1)">{{ mailInfo.recipients }}</a></div>
      </div>
      <!-- <div class="flex-1 mail-item">
        <div class="item-title">收件时间</div>
        <div class="item-content">{{ '2024.08.27 15:03' }}</div>
      </div> -->
      <div class="flex-1 mail-item">
        <div class="item-title">邮件主题</div>
        <div
          class="item-content"
          style="color:rgba(183, 183, 183,1)"
        >{{ '测试版暂未开放' }}</div>
      </div>
      <div class="flex-1 mail-item">
        <div class="item-title">邮件意图</div>
        <div
          class="item-content"
          style="color:rgba(183, 183, 183,1)"
        >{{ '测试版暂未开放' }}</div>
      </div>
      <div class="flex-3 mail-item mail-body">
        <div style="display:flex;width:100%;gap:1vw">
          <div class="item-title flex-1">邮件正文</div>
          <div style="flex:6"></div>
        </div>
        <div
          class="item-content"
          style="white-space: pre-wrap;border: 0.05vw solid #0076b5;padding:1vw;max-height: 10vw;
      overflow: auto;"
        >{{ mailInfo.content }}</div>
      </div>
      <div class="flex-1 mail-item">
        <div class="item-title">附件</div>
        <div
          class="item-content"
          style="color:rgba(183, 183, 183,1)"
        >{{ '测试版暂未开放' }}</div>
      </div>
    </div>
    <div class="flex-5 mail-right mail-card">
      <div
        class="dialogue-content"
        ref="scrollContainer"
      >
        <div
          v-for="(item,index) in contentData"
          :key="index"
        >
          <div
            v-if="item.type===1"
            class="flex-left"
          >
            <div>
              <div
                class="img-box"
                style="margin-right:1vw"
              >
                <img
                  src="@/assets/139mail.jpg"
                  alt=""
                  class="img"
                />
              </div>
            </div>
            <div class="chat-content">
              <div v-if="item.content==='#loading#'">
                <LoadingOutlined />
              </div>
              <div
                v-else
                v-html="markdownTohtml(item.content)"
              ></div>
              <div
                v-if="item.referList?.length>0 && item.referList[0]==='为什么告警？'"
                class="referList"
              >
                <div
                  style="font-weight:bold;cursor: pointer;"
                  @click="dialogueSend"
                >为什么告警？</div>
              </div>
            </div>
          </div>
          <div
            v-else
            class="flex-right"
            :id="item.id"
          >
            <div
              class="chat-content flex-center"
              style="white-space: pre-wrap;"
            >
              {{ item.content }}
            </div>
            <div>
              <div
                class="img-box"
                style="margin-left:1vw"
              >
                <img
                  src="@/assets/man.png"
                  alt=""
                  class="img"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <a-row class="dialogue-send">
        <a-col :span="23">
          <a-textarea
            v-model:value="sendText"
            :auto-size="{ minRows: 1, maxRows: 5 }"
            :bordered="false"
            class="a-textarea-hide-scrollbar"
            :disabled="true"
          />
        </a-col>
        <a-col
          :span="1"
          class="dialogue-send-icon"
        >
          <div class="flex-center icon-box">
            <SendOutlined />
          </div>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
import { faq, getMailById } from '@/api/mail';
import { SendOutlined, LoadingOutlined, RollbackOutlined } from '@ant-design/icons-vue'
import { marked } from 'marked'
export default {
  name: 'mailDetail',
  components: { SendOutlined, LoadingOutlined, RollbackOutlined },
  data () {
    return {
      contentData: [],
      sendText: '测试版暂未开放自由问答',
      faqQueryForm: {
        id: '',
        text: '为什么告警？'
      },
      mailInfo: {
        title: '',
        senders: '',
        sendDate: '',
        recipients: '',
        content: ''
      }
    }
  },
  mounted () {
    this.faqQueryForm.id = this.$route.params.id
    if (this.faqQueryForm.id !== '') {
      getMailById({ id: this.faqQueryForm.id }).then((data) => {
        this.mailInfo = {
          title: data.title,
          senders: data.senders,
          sendDate: data.sendDate,
          recipients: data.recipients,
          content: data.content,
        }
      })
      this.contentData = [
        {
          "id": "0",
          "dialogueId": "0",
          "content": "尊敬的用户，您好！\n\n我是您的智能小帮手，全天候24小时待命，致力于为您提供高效、便捷的邮件安全运营支持。\n\n您可以就本邮件向我发起提问哦！\n\n期待您的每一次互动，让我们共同打造更安全、高效的电子邮件环境。",
          "type": 1,
          "referList": ['为什么告警？']
        }
      ]
    }
  },
  methods: {
    markdownTohtml (text) {
      return marked(text)
    },
    scrollToBottom () {
      const container = this.$refs.scrollContainer
      if (container) {
        container.scrollTop = container.scrollHeight
      }
    },
    dialogueSend () {
      this.contentData.push({
        "id": "0",
        "dialogueId": "0",
        "content": "为什么告警？",
        "type": 0,
        "referList": null
      })
      this.contentData.push({
        id: '1',
        dialogueId: '1',
        content: '#loading#',
        type: 1,
        referList: null
      })
      this.$nextTick(() => {
        this.scrollToBottom()
      })
      faq(this.faqQueryForm).then(() => {
        getMailById({
          id: this.faqQueryForm.id,
        }).then((data) => {
          this.contentData = data.records
          this.$nextTick(() => {
            this.scrollToBottom()
          })
        })
      })
    },
    goBack () {
      this.$router.push('/mail/index')
    }
  }
}
</script>

<style lang="scss" scoped>
.mailDetail {
  display: flex;
  width: 100%;
  height: 100%;
  gap: 1vw;
  padding: 0 2vw;
  .mail-card {
    background: rgba(52, 124, 175, 0.32);
    border-radius: 1vw;
    padding: 1vw 1.5vw;
    display: flex;
    flex-direction: column;
    gap: 0.5vw;
    overflow: auto;
    .back-icon {
      :deep(svg) {
        width: 1.3vw;
        height: 1.3vw;
        color: rgb(55, 179, 101);
      }
    }
    .mail-item {
      display: flex;
      align-items: center;
      gap: 1vw;
      .item-title {
        flex: 1;
        color: white;
        background: rgba(8, 60, 161, 0.44);
        border-radius: 0.4vw;
        padding: 0.25vw;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        font-size: calc(0.12em + 0.76vw);
        box-shadow: rgba(0, 0, 0, 0.4) 0 0.15vw 0.45vw 0;
        border: 0.05vw solid rgba(8, 60, 161, 0.44);
        letter-spacing: 0.05vw;
      }
      .item-content {
        flex: 6;
        color: white;
        font-weight: bold;
        font-size: calc(0.12em + 0.76vw);
        letter-spacing: 0.04vw;
      }
    }
    .mail-body {
      flex-direction: column;
      align-items: flex-start !important;
      gap: 0.5vw;
    }
  }
  .mail-right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .dialogue-content {
      height: 100%;
      width: 100%;
      flex: auto;
      justify-content: flex-start;
      overflow-y: scroll;
      margin-bottom: 1vw;

      -ms-overflow-style: none;
      scrollbar-width: none;

      .flex-left {
        display: flex;
        justify-content: flex-start;
        margin-bottom: 1vw;
      }
      .flex-right {
        display: flex;
        justify-content: flex-end;
        margin-bottom: 1vw;
      }
      .img-box {
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0.05vw solid rgba(0, 0, 0, 0.15);
        padding: 0.1vw;
        border-radius: 0.15vw;
        background: white;
        box-shadow: rgba(0, 0, 0, 0.35) 0.2vw 0.2vw 0.2vw 0;
        .img {
          width: 2vw;
          height: 2vw;
          border: 0.05vw solid rgba(0, 0, 0, 0.15);
          border-radius: 0.1vw;
          margin-left: 0.05vw;
          -webkit-user-drag: none;
        }
      }

      .chat-content {
        background: rgba(0, 102, 255, 0.2);
        max-width: 75%;
        font-size: calc(0.12em + 0.7vw);
        box-shadow: rgba(0, 0, 0, 0.15) 0.2vw 0.2vw 0.2vw 0;
        color: white;
        padding: 0.5vw 1.6vw;
        border-radius: 0.4vw;
        font-family: PingFangSC;
        letter-spacing: 0.05vw;
        .referList {
          margin-bottom: 0.5vw;
          padding: 0.2vw 0.5vw;
          background: rgb(71, 111, 249);
          width: fit-content;
        }
      }
    }
    .dialogue-content::-webkit-scrollbar {
      display: none;
    }
    .dialogue-content {
      -ms-overflow-style: none;
      scrollbar-width: none;
    }
    .dialogue-send {
      flex: none;
      box-shadow: rgba(0, 0, 0, 0.25) 0.15vw 0.15vw 0.15vw 0;
      border-radius: 0.4vw;
      padding: 0.1vw;
      position: relative;
      background: rgba(0, 102, 255, 0.2);
      .a-textarea-hide-scrollbar::-webkit-scrollbar {
        display: none;
      }

      .a-textarea-hide-scrollbar {
        -ms-overflow-style: none;
        scrollbar-width: none;
        margin: 0.2vw;
        color: rgb(183, 183, 183);
      }
      .dialogue-send-icon {
        display: flex;
        align-items: center;
        .icon-box {
          background: rgb(51, 80, 182);
          padding: 0.25vw;
          border-radius: 0.2vw;
          :deep(svg) {
            width: 1vw;
            height: 1vw;
            color: white;
          }
        }
      }
    }
  }
}
</style>